.progream {
	width: 100%;
	height: 100vh;
	padding: 150px 0px;
	display: flex;
	justify-content: center;
	.inner {
		width: 1180px;
		height: 550px;
		overflow: hidden;
		margin: 0px auto;
		position: relative;
		opacity: 0;
		bottom: -100px;
		transition: 1s;

		div.title {
			width: 400px;
			height: 100px;
			position: absolute;
			overflow: hidden;
			margin-top: 30px;
			#title {
				width: calc(100% * 6);
				float: left;
				position: absolute;
				top: 0;
				left: 0;
				z-index: 2;
				text-wrap: nowrap;
				text-align: justify;
				li {
					width: 400px;
					height: 100%;
					float: left;
					h2 {
						font: bold 20px 'Pretendard-Regular';
						color: #888;
						margin-bottom: 10px;
					}
					h1 {
						font: bold 40px 'Pretendard-Regular';
					}

					&.excel {
						h1 {
							color: #217345;
						}
					}
					&.powerpoint {
						h1 {
							color: #d24726;
						}
					}
					&.word {
						h1 {
							color: #185abd;
						}
					}
					&.access {
						h1 {
							color: #a4373a;
						}
					}
					&.teams {
						h1 {
							color: #444bb1;
						}
					}
					&.onedrive {
						h1 {
							color: #0678d3;
						}
					}
				}
			}
		}

		#slider {
			width: calc(100% * 6);
			height: 100%;
			float: left;
			position: absolute;
			top: 120px;

			li {
				width: 1180px;
				height: 100%;
				float: left;

				.pic {
					width: 100%;
					height: 100%;

					article {
						width: 580px;
						height: 400px;
						float: left;
						margin-right: 20px;
						background-color: #fff;
						padding: 40px;
						h2 {
							display: none;
							margin-bottom: 5px;
							font-size: 30px;
						}
						h3 {
							color: black;
							font: bold 30px/1 'Pretendard-Regular';
							margin-bottom: 13px;
						}
						p {
							color: black;
							font: 18px/1.2 'Pretendard-Regular';
							&:nth-of-type(1) {
								margin-bottom: 30px;
							}
						}
						.btn {
							display: flex;
							justify-content: space-around;
							margin-top: 20px;
							button {
								background-color: #fff;
								border-radius: 50px;
								width: 200px;
								height: 35px;
								cursor: pointer;
								font: 15px/35px 'Pretendard-Regular';
								&:last-child {
									color: #fff;
								}
							}
						}

						&:last-child {
							margin-right: 0px;
						}
						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
						}
					}
				}

				&:last-child {
					margin-right: 0px;
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}

				.excel {
					article {
						h2 {
							color: #217345;
						}
						button {
							border: 1px solid #217345;
							&:nth-of-type(2) {
								background-color: #217345;
							}
						}
					}
				}
				.word {
					article {
						h2 {
							color: #185abd;
						}
						button {
							border: 1px solid #185abd;
							&:last-child {
								background-color: #185abd;
							}
						}
					}
				}
				.powerpoint {
					article {
						h2 {
							color: #d24726;
						}
						button {
							border: 1px solid #d24726;
							&:last-child {
								background-color: #d24726;
							}
						}
					}
				}
				.access {
					article {
						h2 {
							color: #a4373a;
						}
						button {
							border: 1px solid #a4373a;
							&:last-child {
								background-color: #a4373a;
							}
						}
					}
				}
				.teams {
					article {
						button {
							border: 1px solid #444bb1;
							&:last-child {
								background-color: #444bb1;
							}
						}
					}
				}
				.onedrive {
					article {
						color: #0678d3;
						button {
							margin-top: 20px;
							border: 1px solid #0678d3;
							&:last-child {
								background-color: #0678d3;
							}
						}
					}
				}
			}
		}

		.prev {
			.fa-angle-left {
				position: absolute;
				top: 57.5%;
				left: 0px;
				color: #000;
				font: 20px 'arial';
			}
		}
		.next {
			.fa-angle-right {
				position: absolute;
				top: 57.5%;
				right: 0px;
				color: #000;
				font: 20px 'arial';
			}
		}
	}
	&.on {
		.inner {
			opacity: 1;
			bottom: 0px;
		}
	}
}

@media screen and (max-width: 539px) {
	.progream {
		width: 100%;
		height: 130vh;
		padding: 100px 0;
		.inner {
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;

			div.title {
				display: none;
			}

			#slider {
				li {
					width: 16.6%;
					.pic {
						article {
							width: 100%;
							height: 100%;
							padding: 10px;
							margin: 0;
							display: flex;
							flex-direction: column;
							h2 {
								display: block;
							}
							h3 {
								font-size: 5vw;
							}
							p {
							}

							.btn {
								display: none;
								position: relative;
								bottom: -400px;
								margin-top: 20px;
								button {
									background-color: #fff;
									border-radius: 50px;
									width: 200px;
									height: 35px;
									cursor: pointer;
									font: 15px/35px 'Pretendard-Regular';
									&:last-child {
										color: #fff;
									}
								}
							}

							img {
								object-fit: contain;
							}
						}
					}
					.excel {
						article {
							h2 {
								color: #217345;
							}
							button {
								border: 1px solid #217345;
								&:nth-of-type(2) {
									background-color: #217345;
								}
							}
						}
					}
					.word {
						article {
							h2 {
								color: #185abd;
							}
							button {
								border: 1px solid #185abd;
								&:last-child {
									background-color: #185abd;
								}
							}
						}
					}
					.powerpoint {
						article {
							h2 {
								color: #d24726;
							}
							button {
								border: 1px solid #d24726;
								&:last-child {
									background-color: #d24726;
								}
							}
						}
					}
					.access {
						article {
							h2 {
								color: #a4373a;
							}
							button {
								border: 1px solid #a4373a;
								&:last-child {
									background-color: #a4373a;
								}
							}
						}
					}
					.teams {
						article {
							button {
								border: 1px solid #444bb1;
								&:last-child {
									background-color: #444bb1;
								}
							}
						}
					}
					.onedrive {
						article {
							color: #0678d3;
							button {
								margin-top: 20px;
								border: 1px solid #0678d3;
								&:last-child {
									background-color: #0678d3;
								}
							}
						}
					}
				}
			}

			.prev {
				top: 50%;
			}
			.next {
				top: 50%;
			}
		}
	}
}
